@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

*{
    margin: 0;
    padding: 0;
    font-family: "Poppins", serif;
}
.container{
    width: 100%;
    height: 100vh;
}

.home {
    background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(28, 25, 25, 0.8)), url("./background.jpg");
    width: 100%;
    height: 100vh;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.navbar h1{
    color: white;
    margin-top: 20px;
    cursor: pointer;
    transition: linear .5s;
}

.navbar h1:hover{
    color: rgb(104, 211, 117);
}

.home .navbar{
    display: flex;
    width: 100%;
    height: 80px;
    justify-content: space-around;
    position: fixed;
    background-color: black;
}

.navbar ul{
    display: flex;
    list-style-type: none;
    margin-top: 20px;
}
.navbar ul li{
    margin-left: 15px;
}
.navbar ul li a{
    text-decoration: none;
    color: white;
    transition: 1s;
}

.navbar ul li a:hover{
    color: rgb(104, 211, 117);
    cursor: pointer;
}

.content-container{
    display: flex;
    width: 100%;
    height: 100vh;
    justify-content: space-evenly;
    align-items: center;
}

.blank
{
    width: 300px;
    height: 100vh;
}

.home .content{
    display: flex;
    width: 100%;
    margin-left: 10px;
    flex-direction: column;
}

.content h1,p,button,a{
    color: white;
    text-decoration: none;
}

.content h1{
    width: 1000px;
    font-weight: 400;
    font-size: 60px;
}

.content p{
    width: 600px;
}
.content button{
    width: 120px;
    height: 50px;
    color: rgb(255, 255, 255);
    border: none;
    border-radius: 6px;
    background-color: rgb(81, 226, 98);
    padding: 10px;
    margin-right: 50px;
    transition: .6s;
}

button:hover{
    background-color: rgb(83, 83, 187);
}

.content .buttons{
    width: 50%;
    height: 100px;
    margin-top: 30px;
}

.menu{
    font-size: 30px;
    margin-top: 20px;
    cursor: pointer;
    display: none;
}

.mobile-nav{
    position: fixed;
}

.mobile-nav ul{
    display: flex;
    flex-direction: column;
    list-style-type: none;
}
.mobile-nav ul li{
    margin-top: 20px;
}
.mobile-nav ul li a{
    text-decoration: none;
    color: white;
    transition: 1s;
}

.mobile-nav ul li a:hover{
    color: rgb(104, 211, 117);
    cursor: pointer;
}

.features{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    align-items: center;
}
.features h3{
    color: rgb(104, 211, 117);
    font-weight: 450;
    font-size: 16px;
}
.features h2{
    color: black;
    font-weight: 420;
    font-size: 25px;
}
.feature-container{
    display: flex;
    width: 100%;
    height: 100vh;
    margin-top: 20px;
}
.feature-container .features-images{
    display: flex;
    flex-wrap: wrap;
    width: 60%;
    margin-bottom: 20px;
    
}
.feature-container .features-images .img{
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    margin: 5px;
}
.feature-container .features-images .img img{
    flex-basis: 40%;
    width: 280px;
    height: 180px;
}

.feature-container .features-images .img p{
    text-align: center;
    color: black;
}
.feature-container .features-images .img:nth-child(4)
{
    text-align: center;
    margin-left: 8px;
    width: 420px;
}
.feature-container .features-images .img:nth-child(5)
{
    text-align: center;
    width: 420px;
}
.whyus{
    width: 40%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgb(83, 83, 187);
}
.whyus img{
    width:400px;
    height: 400px;
}
.whyus p{
    color: white;
   margin: 20px;
}
.whyus button{
    width: 120px;
    height: 50px;
    color: rgb(255, 255, 255);
    border: none;
    border-radius: 6px;
    background-color: rgb(49, 137, 144);
    transition: .6s;
}

.whyus button:hover{
    background-color: rgb(50, 190, 104);
}

.about{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.about-content{
    display: flex;
    width: 100%;
    height: 100vh;
}
.aboutus{
    display: flex;
    flex-direction: column;
    width: 40%;
    height: 100vh;
    align-items: center;
    background-color: rgb(50, 190, 104);
}
.about-container{
    width: 60%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.about-container button{
    width: 120px;
    height: 50px;
    color: rgb(255, 255, 255);
    border: none;
    border-radius: 6px;
    background-color: rgb(49, 137, 144);
    transition: .6s;
}

.about-container p{
    color: black;
    width: 80%;
}

.about-container img{
    width: 230px;
    height: 230px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    border-radius: 50%;
    margin-right: 10px;
}
.about-container img:hover{
    box-shadow: rgb(9, 188, 90) 1px 1px 2px 0px, rgba(0, 254, 59, 0.15) -1px -1px 3px 1px;
    filter: blur(1.2px);
}
.about-container button:hover{
    background-color: rgb(50, 190, 104);
}

