table{
    border: 2px solid rgb(50, 190, 104);
    padding: 20px;
    width: 350px;
    height: 450px;
    border-radius: 8px;
}

table td input{
    padding: 20px;
    outline: none;
    width: 85%;
    border-radius: 7px;
    border: 1px solid rgb(50, 190, 104);
}
table td textarea{
    padding: 20px;
    outline: none;
    border-radius: 7px;
    border: 1px solid rgb(50, 190, 104);
    width: 85%;
}
table td button{
    width: 120px;
    height: 50px;
    color: rgb(255, 255, 255);
    border: none;
    border-radius: 6px;
    background-color: rgb(49, 137, 144);
    transition: .6s;
}
table td button:hover{
    background-color: rgb(50, 190, 104);
}
table td input::placeholder{
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

footer{
    display: flex;
    flex-direction:column;
    background-color: rgb(3, 35, 60);
}

footer .div1{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
footer .div1 h1{
    color: white;
    font-weight: 400;
}
footer .div1 p{
    width: 40%;
    text-align: center;
    margin-top: 20px;
    color: #888;
}
footer .input{
    display: flex;
    margin-top: 20px;
}

footer .input input{
    padding: 10px;
    width: 400px;
    color: white;
    background-color: rgb(113, 148, 174);
    border-radius: 7px;
    outline: none;
    border: none;
}
footer .input button{
    width: 120px;
    height: 50px;
    color: rgb(255, 255, 255);
    border: none;
    border-radius: 6px;
    background-color: rgb(49, 137, 144);
    transition: .6s;
    margin-left: 10px;
}
footer .input button:hover{
    background-color: rgb(50, 190, 104);
}

footer .div2{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 250px;
    justify-content: space-around;
}
footer .div2 .social{
    display: flex;
    flex-direction: column;
    width: 50%;
}
.icons{
    display: flex;
}
footer .div2 .social .icons p{
    color: #999;
    width: 50px;
    height: 50px;
    background-color: #111;
    margin-left: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
    border-radius: 50%;
    transition: .6s;
}
footer .div2 .social .icons p:hover{
    color: rgb(50, 190, 104);
    transform:translateY(-15px);
}
.social h2{
    color: white;
    font-weight: 400;
}
.social p{
    color: #999;
    margin-top: 20px;
}

.social .ourpages{
    display: flex;
    flex-direction: column;
}
.ourpages h2{
    color: white;
}

.ourpages ul li{
    margin-top: 10px;
    list-style-type: none;
}
.ourpages ul li a{
    color: #999;
    transition: .6s;
}
.ourpages ul li a:hover{
    color: rgb(50, 190, 104);
}
.hr{
    width: 100%;
    height: 1px;
    display: flex;
    justify-content: center;
}
hr{
    color: #999;
    width: 90%;
}
footer .div3{
    text-align: center;
    color: #999;
    margin-top: 10px;
    margin-bottom: 10px;
}
@media screen and (max-width:700px){
    .blank{
        display: none;
    }
    .home .navbar ul{
        display: none;
    }
    .home{
        overflow: hidden;
    }
    .menu{
        display: block;
    }
    .navbar{
        background-color: rgb(56, 129, 194);
    }
    .navbar h1,p{
        color: rgb(255, 255, 255);
        height: 30px;
    }
    .home .content h1{
        width: 450px;
        font-size: 50px;
    }
    .home .content p{
        width: 450px;
        color: rgb(163, 162, 162);
        margin-bottom: 30px;
    }
    .mobile-nav{
        top: 60px;
        display: flex;
        flex-direction: column;
        background-color: rgb(253, 253, 253);
        width: 100%;
        align-items: center;
        transition: ease-in 1s;
    }
    .mobile-nav ul li a{
        color: black;
    }
    .content .buttons{
        width: 100%;
    }
    .feature-container {
        flex-direction: column;
        align-items: center;
    }
    .feature-container .whyus img{
        width: 400px;
        height: 300px;
    }
    .whyus{
        width: 100%;
        height: 600px;
        align-items: center;
        background-color: rgb(83, 83, 187);
    }
    .whyus p{
        width: 100%;   
    }
    .whyus img{
        width: 500px;
    }
    .feature-container .features-images .img {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
        margin-bottom: 5px;
    }
    .features{
        height: 290vh;
    }
    .about{
        height: 200vh;
    }
    .about-content{
        flex-direction: column;
        align-items: center;
    }
    .about-content .about-container{
        width: 100%;
    }
    .aboutus {
        width: 100%;
    }
    .about-content .aboutus img{
        width: 100%;
    }
    .whyus button{
        margin-top: 50px;
        margin-bottom: 10px;
    }
    .about-container button{
        margin-top: 170px;
        padding: 10px;
    }
    .about-container .images{
        display: flex;
        flex-direction: column;
        width: 100%;
        align-items: center;
        margin-top: 260px;
    }
    .about-container .images img{
        margin-right: 10px;
        margin-top: 20px;
    }
    .services{
        height: 230vh;
    }
    .service-container .div1{
        flex-direction: column;
        height: 600px;
        align-items: center;
    }
    .service-container .div2{
        flex-direction: column;
        height: 600px;
        align-items: center;
    }
    .service-container .div3{
        flex-direction: column;
        height: 600px;
        align-items: center;
    }
    .service-container .div1 div p{
        width: 430px;
    }
    .service-container .div1 div button{
        margin-top: 50px;
        margin-bottom: 20px;
    }
    .service-container .div2 div p{
        width: 430px;
    }
    .service-container .div2 div button{
        margin-top: 50px;
        margin-bottom: 20px;
    }
    .service-container .div3 div p{
        width: 430px;
    }
    .service-container .div3 div button{
        margin-top: 50px;
        margin-bottom: 20px;
    }

    .review{
        flex-direction: column;
        align-items: center;
        height: 100vh;
    }
    .testominials{
        margin-top: 100px;
        height: 100vh;
    }
    .review .customer1 , .customer2 , .customer3{
        margin-bottom: 10px;
    }
    .contact{
        margin-top: 200px;
    }
    .contactus{
        flex-direction: column-reverse;
    }
    .hereimg{
        display: none;
    }
    footer{
        width: 100%;
        height: 100vh;
        flex-direction: column;
    }
    footer .div1 .input input{
        width: 330px;
    }
    footer .div2{
